
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name: "edit",
props: ["id"],
data: function() {
return {
topic: {
title: "",
content: ""
}
};
},
methods: {

edittopic: function() {
var app = this;
this.$http.post('/topics/update',{
topic_id:this.topic_id,
title:this.topic_id,
content:this.topic_id,
accesstoken:
this.$store.state.accesstoken
}).then(function(response){
console.log(response.data);

app.$router.back();
});
}
},
components: {
Group,
XTextarea,
XButton,
mavonEditor

},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http.get("/topic/" + to.params.id).then(function(response) {
vm.topic = response.data.data;
});
});
},
beforeRouteUpdata: function (to, from, next) {
var app = this;
app.$http.get('/topic/' + to.params.id).then(function (response) {
app.topic = response.data.data;
});
next();
}
};



