import Vue from 'vue';
import VueRouter from 'vue-router';
import Topiclist from '@/components/TopicList.vue';
import Topic from '@/components/Topic.vue';
import Login from '@/components/Login.vue';
import Logout from '@/components/Logout.vue';
import Center from '@/components/Center.vue';
import Msg from '@/components/Msg.vue';
import User from '@/components/User.vue';
import Create from '@/components/Create.vue';
import Edit from '@/components/Edit.vue';
Vue.use(VueRouter);

const routes = [{
    path: '/',
    redirect: '/topiclist/all'
  }, // 根目录默认访问topiclist/all  重定向根目录
  {
    path: '/topiclist/:name',
    name: 'topiclist',
    component: Topiclist,
    props: true
  },
  {
    path: '/topic/:id',
    name: 'topic',
    component: Topic,
    props: true
  },
  // 登录操作
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  // 个人中心
  {
    path: '/center',
    name: 'center',
    component: Center
  },
  {
    path: '/user/:loginname',
    name: 'user',
    component: User,
    props: true
  },
  // 消息通知
  {
    path: '/msg',
    name: 'msg',
    component: Msg
  },
  // 登出操作
  {
    path: '/logout',
    name: 'logout',
    component: Logout
  },
  // 发表新话题
  {
    path: '/create',
    name: 'create',
    component: Create
  },
  {
    path: '/topic/:id/edit',
    name: 'edit',
    component: Edit,
    props: true
  }
];

const router = new VueRouter({
  routes
});
export default router;


